import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { signOut } from 'aws-amplify/auth';
import { useNavigate } from 'react-router-dom';

import avatar2 from '../../assets/images/user/avatar-2.jpg';

const Topbar = () => {
  const navigate = useNavigate();
  const user = JSON.parse(sessionStorage.getItem('userSession'));
  useEffect(() => {
    if (!user) {
      alert('Session timeout. Please login again.');
      navigate('/login');
    }
  }, [user, navigate]);

  
  const handleLogout = async () => {
    try {
      await signOut({global: true});
      sessionStorage.removeItem('userSession');
      alert('Success', 'You have been signed out.');
      navigate('/login');
    } catch (error) {
      alert('Error', 'Sign-out failed. Please try again.');
    }
  };
  return (
    <div className="navbar-custom">
      <div className="topbar">
        <div className="topbar-menu d-flex align-items-center gap-1">
          <div className="logo-box">
            <a href="/device" className="logo-light">
              {/* <img
                src="assets/images/logo-light.png"
                alt="logo"
                className="logo-lg"
              /> */}
              <img
                src="assets/images/logo-sm.png"
                alt="small logo"
                className="logo-sm"
              />
            </a>

            <a href="/device" className="logo-dark">
              <img
                src="assets/images/logo.png"
                alt="dark logo"
                className="logo-lg"
              />
              <img
                src="assets/images/logo-sm.png"
                alt="small logo"
                className="logo-sm"
              />
            </a>
          </div>
  {/* -------=Menu-Button=------- */}
          {/* <button className="button-toggle-menu">
            <i className="mdi mdi-menu"></i>
          </button> */}

        </div>

        <ul className="topbar-menu d-flex align-items-center">
          
          <li className="dropdown">
            <a
              className="nav-link dropdown-toggle nav-user me-0 waves-light"
              data-bs-toggle="dropdown"
              href="#"
              role="button"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <img src={avatar2} className="img-radius" alt="User Profile" 
              />
              <span className="ms-1 d-none d-md-inline-block white">
              {user && <span>{user.custom_uname}</span>} 
              <i className="mdi mdi-chevron-down ms-1"></i>
              </span>
            </a>
            <div className="dropdown-menu dropdown-menu-end profile-dropdown ">
              <div className="dropdown-header noti-title">
                <h6 className="text-overflow m-0">Welcome !</h6>
              </div>

              <a
                href="/profile"
                className="dropdown-item notify-item"
              >
                <i className="fe-user"></i>
                <span>My Profile</span>
              </a>

              
              <div className="dropdown-divider"></div>

                <Link to="#" className="dropdown-item notify-item" title="Logout" onClick={handleLogout}>
                <i className="fe-log-out"></i>
                <span>Logout</span>
                </Link>
            </div>
          </li>

        </ul>
      </div>
    </div>
  );
};

export default Topbar;
