// routes.js
import React, { lazy } from "react";

// Lazy load components for performance optimization
//const HomePage = lazy(() => import("../components/pages/Home"));
const Device = lazy(() => import("../components/pages/Device"));
const LoginPage = lazy(() => import("../components/pages/Login"));
const SignUpPage = lazy(() => import("../components/pages/SignUp"));
const Sensordetails = lazy(() => import("../components/pages/Sensordetails"));
const Profile = lazy(() => import("../components/pages/Profile"));

const routes = [
  {
    path: "/login",
    element: <LoginPage />,
    exact: true,
  },
  {
    path: "/signup",
    element: <SignUpPage />,
    exact: true,
  },
  // {
  //   path: "/",
  //   element: <HomePage />, // Default home route
  //   exact: true,
  // },
  {
    path: "/device",
    element: <Device />,
    exact: true,
  },
  {
    path: "/sensor-details",
    element: <Sensordetails />,
    exact: true,
  },
  {
    path: "/profile",
    element: <Profile />,
    exact: true,
  },
];

export default routes;
